@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

.placeholderContainer {
    display: flex;
    justify-content: center;
    pointer-events: none;
}

.placeholder {
    position: fixed;
    top: 15vh;
    width: 100vw;
    max-width: 800px;
}

@media only screen and (max-width: 600px) {
    .placeholder {
        top: 5vh;
    }
  }

.copyConfirmationContainer {
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: center;
}

.copyConfirmation {
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 3px 19px 46px;
    border-radius: 16px;
    height: 25px;
    width: 70px;
    padding: 5px;
    top: 100px;
    background: white;
    box-shadow: 4px 4px 6px gray;
    font-family: "Montserrat", sans-serif;
}

