@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{overflow:auto;font-family:"Montserrat", sans-serif}.editorContainer{display:flex;justify-content:center}textarea{font-size:large;font-family:'Montserrat';resize:none;margin-top:40px;border:none;overflow:auto;outline:none;box-shadow:none;resize:none;height:99vh;width:50vw}@media only screen and (max-width: 600px){textarea{width:85vw}}

.infoFabButton{background:white;text-align:center;width:50px;height:50px;border-radius:100%;display:inline-flex;flex-direction:column;justify-content:center;align-items:center;font-size:30px;color:#666666;font-weight:bold;text-decoration:none;box-shadow:5px 10px 20px;float:right;margin-left:80%;bottom:12px;position:fixed}.fabButtonContainer{height:100%;width:100%}

.placeholderContainer{display:flex;justify-content:center;pointer-events:none}.placeholder{position:fixed;top:15vh;width:100vw;max-width:800px}@media only screen and (max-width: 600px){.placeholder{top:5vh}}.copyConfirmationContainer{position:fixed;width:100%;display:flex;justify-content:center}.copyConfirmation{display:flex;justify-content:center;align-items:center;box-shadow:3px 19px 46px;border-radius:16px;height:25px;width:70px;padding:5px;top:100px;background:white;box-shadow:4px 4px 6px gray;font-family:"Montserrat", sans-serif}#homeContainer{height:100vh}

.logoContainer{display:flex;justify-content:flex-start}.logo{max-width:300px}.aboutContainer{display:flex;justify-content:center;font-weight:400;font-size:22px;font-family:"Montserrat", sans-serif}.aboutContent{height:90vh;display:flex;flex-direction:column;margin-top:5vh;padding:0 30px 0 30px}.aboutParagraph,.authors{padding-left:25px}.authors{font-size:18px}a:link{text-decoration:none}


